@font-face {
  font-family: SourceCodePro;
  src: inline('fonts/Source_Code_Pro/SourceCodePro-Regular.ttf');
}

.navbar {
  background-color: #f8f8f8;
  border-color: #e7e7e7;

  &:before,&:after {
    content: none
  }

  .container {
    &:before, &:after {
      content: none;
    }

    &.container-override {
      padding-left: inherit;
      padding-right: inherit;
    }
  }

  .navbar-brand.navbar-brand-override {
    float: inherit;
    height: inherit;
    padding: inherit;
    font-size: inherit;
    line-height: inherit;
  }

  .navbar-item {
    a {
      &:link,&:visited,&:hover {
        color: #777;
        text-decoration: none;
      }

      &:hover {
        cursor: pointer;

        svg[role="img"] {
          fill: var(--accent);
        }

        svg.feather-mail,svg.feather-music {
          stroke: var(--accent);
        }
      }
    }
  }
}

.home {
  display: flex;
  align-items: center;

  .title {
    font-family: SourceCodePro;
  }
}

@media (prefers-color-scheme: dark) {
  html body {
    background-color: #222222;
    min-height: 100%;
    height: auto;
    color: white;
  }

  .navbar {
    background-color: #242424;
    border-color: #101010;

    .navbar-item {
      a {
        &:link,&:visited,&:hover {
          color: #777;
          text-decoration: none;
        }
      }

      &:hover {
        svg[role="img"] {
          fill: #ff7600;
        }
        svg.feather-mail,svg.feather-music {
          stroke: #ff7600;
        }
      }
    }
  }

  .content {
    h1,h2,h3,h4,h5,h6 {
      color: white;
    }
    blockquote {
      background-color: #333333;
    }
  }

  .tag:not(body) {
    background-color: #4a4a4a;
  }

  code {
    color: #ff7600;
    background-color: #101010;
  }

  a:link, a:visited, a:hover {
    color: #ff7600 !important;
  }

  .title {
    color: white;
  }
}
